import mitt from 'mitt'
import LOG from './Log';
import {getServerList} from './ChooseServer';

const CONNECTED = 'connected';
const CONNECTING = 'connecting';
const DISCONNECTED = 'disconnected';

class Socket {
  constructor(option, emitter) {
    this.serverlist = [];
    this.option = option;
    this.index = 0;
    this.isInit = true;
    this.state = DISCONNECTED;
    this.hasInvokeClose = false;

    this.emitter = emitter;
    this.on = this.emitter.on;
    this.off = this.emitter.off;
    this.emit = this.emitter.emit;
  }

  connect() {
    this.state !== DISCONNECTED && this.close();
    getServerList(this.option).then(res => {
      const {serverlist, url} = res;
      LOG.debug('Get server list from ' + url);
      this.index = 0;
      this.serverlist = serverlist;
      if(this.option && this.option.servers) {
        this.serverlist = this.option.servers;
      }
      LOG.debug('Connect server with list: ', this.serverlist);
      this.connectWorker(this.serverlist[this.index]);
    }).catch(err => {
      this.emit && this.emit('socketError', err);
    });
  }

  connectWorker() {
    this.state = CONNECTING;
    let url = this.serverlist[this.index];
    // url = 'wss://miniapp.agoraio.cn/58-211-82-170/api';
    LOG.debug('Connect server: ', url);

    let {
      sid,
      channel,
      uid,
      reportManager
    } = this.option

    this.ws = new wx.connectSocket({
      url: url,
      complete: res => LOG.debug('wxSocket: complete: ', res),
      fail: res => {
        LOG.debug('wxSocket: fail: ', res);
        if(reportManager) {
          reportManager.report('connectWebSocket', {
            header: {
              sid: sid,
              cname: channel, 
              uid: uid,
              success: false
            },
            body: {
              wsurl: url,
              ec: JSON.stringify(res)
            }
          })
        }
      },
      success: res => {
        LOG.debug('wxSocket success: ', res)
        if(reportManager) {
          reportManager.report('connectWebSocket', {
            header: {
              sid: sid,
              cname: channel, 
              uid: uid,
              success: true
            },
            body: {
              wsurl: url,
            }
          })
        }
      },
    });
    this.ws.onOpen(this._onopen.bind(this));
    this.ws.onMessage(this._onmessage.bind(this));
    this.establishWSTimeoutTimer = setTimeout(() => {	
      if(this.state !== CONNECTED) {	
        this.connectNextWorker();
      }	
    }, 5000);
  }

  connectNextWorker() {
    this.state !== DISCONNECTED && this.close();
    LOG.debug(this.index, this.serverlist.length)
    if(this.index >= this.serverlist.length-1) {
      this.emit && this.emit('socketError', {
        code: 905,
        reason: 'connect websocket failed',
        wsurl: JSON.stringify(this.serverlist),
      });
    } else {
      this.index++;
      this.connectWorker();
    }
  }

  _onopen() {
    LOG.info('websocket onopen');
    clearTimeout(this.establishWSTimeoutTimer);
    this.state = CONNECTED;
    this.isInit = false;
    // ws连接的过程中，即使调用了ws.close以后，ws还是可能成功连上，并且触发onopen，导致一系列未定义行为，
    // 所以这个根据 hasInvokeClose 来阻止下面的操作
    if(this.hasInvokeClose) {
      LOG.debug('Alread invoke ws.Close, will not throw onopen event');
      return;
    }
    this.ws.onClose(this._onclose.bind(this));
    this.emit && this.emit('onopen');
  }

  _onclose(err) {
    LOG.info('websocket onclose:' + JSON.stringify(err));
    this.emitter.emit('_wsClose');
    clearTimeout(this.establishWSTimeoutTimer);
    this.state = DISCONNECTED;
    // 这里的err.reason是在调用ws.close传的参数，但是小程序有bug，reason在这里是null，但还是先这样写。
    if(err && err.reason == 'close') return;
    /** 
     * ws的onclose触发有3种情况
     * 1、主动调用了ws.close，此时触发的 onClose 事件不需要向上抛出，直接return; -> return;
     * 2、在链接ws的时候，没连上断开，这个时候需要重连，会调用 connectNextWorker -> this.connectNextWorker();
     * 3、已经连上ws，但是因为别的原因断开，此时触发的 onClose 事件需要向上抛出 -> this.emit('onclose')
     */  
    if(this.hasInvokeClose) return;
    if(this.isInit) {
      this.connectNextWorker();
    } else {
      this.emit && this.emit('onclose');
    }
  }

  _onmessage(msg) {
    if(this.hasInvokeClose) return;
    LOG.blind('receive message: ', JSON.stringify(msg));
    if(this.isConnected() && this.emit) {
      this.emit('onmessage', msg);
    }
  }

  isConnected() {
    return this.state === CONNECTED;
  }

  sendMessage(msg) {
    LOG.blind('send message: ', msg);
    if(this.isConnected() && this.ws) {
      this.ws.send({
        data: msg
      });
    }
  }

  _checkServerList(serverlist) {
    if(serverlist instanceof Array == false || serverlist.length == 0) {
      return false;
    } else {
      return true;
    }
  }

  destroy(callback) {
    if(this.state !== DISCONNECTED) {
      this.close();
    }
    const closeCallback = () => {
      this.off('_wsClose', closeCallback);
      this.serverlist = null;
      this.ws = null;

      this.emitter = null;
      this.on = null;
      this.off = null;
      this.emit = null;
      callback && callback();
    }
    this.on('_wsClose', closeCallback);
  }

  close() {
    LOG.debug('Close websocket');
    clearTimeout(this.establishWSTimeoutTimer);
    this.hasInvokeClose = true;
    this.state = DISCONNECTED;
    this.ws && this.ws.close({
      reason: 'close'
    });
    // this.ws && this.ws.close();
    this.ws = null;
  }

}


export default Socket
