const checkValidEnum = function(value, name, list) {
  for(var i=0; i < list.length; i++) {
    if(value === list[i]) return true;
  }
  throw new Error(`${name} can only be set as ${JSON.stringify(list)}`);
}

const checkValidObject = function(object, name) {
  if(!object) {
    throw new Error(`Invalid param: ${name || 'param'} cannot be empty`);
  } else if(typeof object !== 'object') {
    throw new Error(`${name || 'This paramter'} is of the object type`);
  }
  return true;
}

const checkValidString = function(string, name, lenMin, lenMax, ascii) {
  if(isEmpty(lenMin)) {
    lenMin = 1;
  }
  lenMax = lenMax || 255;
  if (isEmpty(ascii)){
    ascii = true;
  }

  if(isEmpty(string)) {
    throw new Error(`${name || 'param'} cannot be empty`);
  } else {
    if(!isValidString(string, lenMin, lenMax, ascii)) {
      throw new Error(`Invalid ${name || 'string param'}: Length of the string: [${lenMin},${lenMax}].${ascii ? " ASCII characters only.": ""}`);
    }
  }
}

const checkValidNumber = function(number, name, lenMin, lenMax) {
  if(isEmpty(lenMin)) {
    lenMin = 1;
  }
  lenMax = lenMax || 10000;

  if(isEmpty(number)) {
    throw new Error(`${name || 'param'} cannot be empty`);
  } else {
    if(!isValidNumber(number, lenMin, lenMax)) {
      throw new Error(`Invalid ${name || 'number param'}: The value range is [${lenMin},${lenMax}]. integer only`);
    }
  }
}

const checkValidFloatNumber = function(number, name, lenMin, lenMax) {
  if (number == null) {
    throw new Error(`${name || 'param'} cannot be null`);
  }

  if(isEmpty(lenMin)) {
    lenMin = 0;
  }
  lenMax = lenMax || 10000;

  if(isEmpty(number)) {
    throw new Error(`${name || 'param'} cannot be empty`);
  } else {
    if(!isValidFloatNumber(number, lenMin, lenMax)) {
      throw new Error(`Invalid ${name || 'number param'}: The value range is [${lenMin},${lenMax}].`);
    }
  }
}

const checkValidBoolean = function(boolean, name) {
  if(isEmpty(boolean)) {
    throw new Error(`${name || 'param'} cannot be empty`);
  } else {
    if(!isValidBoolean(boolean)) {
      throw new Error(`Invalid ${name || 'boolean param'}: The value is of the boolean type.`);
    }
  }
}

const isValidString = function(string, lenMin, lenMax, ascii) {
  if (!lenMin){
    lenMin = 0;
  }
  if (!lenMax){
    lenMax = Number.MAX_SAFE_INTEGER;
  }
  if (isEmpty(ascii)){
    ascii = true;
  }
  return isString(string) && (ascii ? isASCII(string) : true) && string.length >= lenMin && string.length <= lenMax;
}

const isValidNumber = function(number, minimum, maximum) {
  return isInteger(number) && number >= minimum && number <= maximum;
}

const isValidFloatNumber = function(number, minimum, maximum) {
  return isNumber(number) && number >= minimum && number <= maximum;
}

const isValidBoolean = function(boolean) {
  return typeof boolean === 'boolean';
}

const isValidToken = function(token) {
  return isValidString(token, 1, 2047)
}

const isValidChannelName = function(cname) {
  // https://docs.agora.io/en/Voice/API%20Reference/web/interfaces/agorartc.client.html#join
  return isString(cname) && /^[a-zA-Z0-9 \!\#\$\%\&\(\)\+\-\:\;\<\=\.\>\?\@\[\]\^\_\{\}\|\~\,]{1,64}$/.test(cname);
}

const isASCII = function(string) {
  if(typeof string !== 'string') return;
  for( var i = 0; i < string.length; i++) {
    var chr = string.charCodeAt(i);
    if( chr < 0 || chr > 255 ) {
      return false;
    }
  }
  return true;
}

const isInteger= function(number) {
  return typeof number === 'number' && number % 1 === 0;
}

const isNumber= function(number) {
  return typeof number === 'number';
}

const isString = function(string) {
  return typeof string === 'string';
}

const isArray = function(array) {
  return array instanceof Array
}

const isEmpty = function(object) {
  return object === null || object === undefined;
}

export {
  checkValidObject,
  checkValidString,
  checkValidNumber,
  checkValidFloatNumber,
  checkValidBoolean,
  checkValidEnum,

  isValidString,
  isValidNumber,
  isValidBoolean,

  isASCII,
  isInteger,
  isNumber,
  isString,
  isArray,
  isEmpty,

  isValidToken,
  isValidChannelName
}
