import { VERSION } from './Config'

class MessageManager {
  constructor(option) {
    this.appId = option.appId;
    this.channel = option.channel;
    this.uid = option.uid + '';
    this.sid = option.sid;
    
    this.seq = 1;
    this.requestId = 2;
  }

  setSid(sid) {
    this.sid = sid;
  }

  _getWorkerManagerMsg() {
    return {
      appId: this.appId,
      cname: this.channel,
      uid: this.uid, 
      sid: this.sid,
      sdkVersion: VERSION,
      seq: ++this.seq,
      requestId: ++this.requestId,
      ts:  Math.floor(Date.now() / 1000)
    }
  }

  send(opt) {
    let msg = this._getWorkerManagerMsg();
    msg.clientRequest = opt
    return msg;
  }

  updatePushUrl() {
    let msg = this._getWorkerManagerMsg();
    msg.clientRequest = {
      action: 'update_url',
      role: 'publish',
      uid: +this.uid
    }
    return msg;
  }

  ping() {
    return {
      command: 'ping',
      requestId: ++this.requestId,
      appId: this.appId,
      cname: this.channel,
      uid: this.uid + '', 
      sid: this.sid, 
      ts: Math.floor(Date.now() / 1000)
    }
  }

  ping2() {
    let msg = this._getWorkerManagerMsg();
    msg.clientRequest = {
      action: 'ping',
    }
    return msg;
  }

  setRole(role) {
    let msg = this._getWorkerManagerMsg();
    msg.clientRequest = {
      action: 'set_role',
      role: role
    }
    return msg;
  }

  rejoin(appId, cname, uid, dynamicKey, uids, role) {
    let msg = this._getWorkerManagerMsg();
    msg.clientRequest = {
      action: 'join',
      role: role,
      appId: appId,
      key_vocs: dynamicKey || appId,
      key_vos: dynamicKey || appId,
      channel_name: cname,
      uid: uid + '',
      known_speakers: uids
    }
    return msg;
  }

  join(appId, cname, uid, dynamicKey, role) {
    let msg = this._getWorkerManagerMsg();
    msg.clientRequest = {
      action: 'join',
      role: role,
      appId: appId,
      key_vocs: dynamicKey || appId,
      key_vos: dynamicKey || appId,
      channel_name: cname,
      uid: uid + ''
    }
    return msg;
  }
  
  muteLocal(uid, target) {
    let msg = this._getWorkerManagerMsg();
    msg.clientRequest = {
      action: 'mute_local',
      uid: +uid,
      target: target,
      mute: true
    }
    return msg;
  }

  unmuteLocal(uid, target) {
    let msg = this._getWorkerManagerMsg();
    msg.clientRequest = {
      action: 'mute_local',
      uid: +uid,
      target: target,
      mute: false
    }
    return msg;
  }

  mute(uid, target) {
    let msg = this._getWorkerManagerMsg();
    msg.clientRequest = {
      action: 'mute',
      uid: +uid,
      target: target
    }
    return msg;
  }

  unmute(uid, target) {
    let msg = this._getWorkerManagerMsg();
    msg.clientRequest = {
      action: 'unmute',
      uid: +uid,
      target: target
    }
    return msg;
  }
  
  publish(uid) {
    let msg = this._getWorkerManagerMsg();
    msg.clientRequest = {
      action: 'publish',
      uid: +uid
    }
    return msg;
  }
  
  unpublish(uid) {
    let msg = this._getWorkerManagerMsg();
    msg.clientRequest = {
      action: 'unpublish',
      uid: +uid
    }
    return msg;
  }
  
  subscribe(uid) {
    let msg = this._getWorkerManagerMsg();
    msg.clientRequest = {
      action: 'subscribe',
      uid: +uid
    }
    return msg;
  }
  
  unsubscribe(uid) {
    let msg = this._getWorkerManagerMsg();
    msg.clientRequest = {
      action: 'unsubscribe',
      uid: [+uid]
    }
    return msg;
  }
  
  leave(uid) {
    let msg = this._getWorkerManagerMsg();
    msg.clientRequest = {
      action: 'leave',
      uid: +uid
    }
    return msg;
  }

}

export default MessageManager;