import { SDK_VERSION } from './Config.js';
import LOG from './Log';

const ReportServiceClient = require('../report_service/gen-js/ReportService').ReportServiceClient;
const Thrift = require('../report_service/Thrift/thrift').default;
const VosdkHeader = require('../report_service/gen-js/VosdkCommon_types').VosdkHeader;
const ReportItem = require('../report_service/gen-js/report_item_miniapp_types');


class ReportManager {
  constructor() {
    this.transport = new Thrift.TXHRTransport("https://uni-webcollector.agora.io/report");
    this.protocol  = new Thrift.TJSONProtocol(this.transport);
    this.client    = new ReportServiceClient(this.protocol);
    this.seqId = 0;
    this._cache = [];
    this.isReporting = false;
    this.retryCount = 0;
  }

  generateHeader(header) {
    if(header && header.sid) {
      return new VosdkHeader({
        sid: header.sid,
        cname: header.cname || '', 
        lts: header.lts, 
        elapse: header.lts - this.startTime,
        uid: header.uid,
        success: header.success
      })
    }
  }

  report(eventName, option) {
    let header = null;
    if(option.header) {
      option.header.lts = +new Date();
      if(eventName === 'sessionStart') {
        this.startTime = option.header.lts
      }
      header = this.generateHeader(option.header);
    }
  
    if(option.body && header) {
      let body = option.body;
      var eventItem;
      switch(eventName) {
        case 'sessionStart':
          eventItem = new ReportItem({
            miniappSession: true
          });
          eventItem.uri = 8000;
          eventItem['miniappSession'].header = header;
          eventItem['miniappSession'].appid = body.appId ;
          eventItem['miniappSession'].uname = body.uname;
          eventItem['miniappSession'].ver = SDK_VERSION;
          eventItem['miniappSession'].dk = body.dk;
          eventItem['miniappSession'].details = JSON.stringify({seq: this.seqId++});
          break;
  
        case 'chooseServer':
          eventItem = new ReportItem({
            miniappJoinChooseServer: true
          });
          eventItem.uri = 8001;
          eventItem['miniappJoinChooseServer'].header = header;
          eventItem['miniappJoinChooseServer'].csurl = body.csurl ;
          eventItem['miniappJoinChooseServer'].ec = body.ec;
          eventItem['miniappJoinChooseServer'].gatewaylist = body.gatewaylist;
          eventItem['miniappJoinChooseServer'].details = JSON.stringify({seq: this.seqId++});
          break;
        
        case 'connectWebSocket':
          eventItem = new ReportItem({
            miniappConnectWebSocket: true
          });
          eventItem.uri = 8002;
          eventItem['miniappConnectWebSocket'].header = header;
          eventItem['miniappConnectWebSocket'].ec = body.ec;
          eventItem['miniappConnectWebSocket'].wsurl = body.wsurl;
          eventItem['miniappConnectWebSocket'].details = JSON.stringify({seq: this.seqId++});
          break;
        
        case 'join':
          eventItem = new ReportItem({
            miniappJoin: true
          });
          eventItem.uri = 8003;
          eventItem['miniappJoin'].header = header;
          eventItem['miniappJoin'].ec = body.ec;
          eventItem['miniappJoin'].isrejoin = body.isrejoin;
          eventItem['miniappJoin'].details = JSON.stringify({
            seq: this.seqId++,
            appId: body.appId,
            wsurl: body.wsurl
          });
          break;
        
        case 'publish':
          eventItem = new ReportItem({
            miniappPublish: true
          });
          eventItem.uri = 8004;
          eventItem['miniappPublish'].header = header;
          eventItem['miniappPublish'].ec = body.ec;
          eventItem['miniappPublish'].pushurl = body.pushurl;
          eventItem['miniappPublish'].details = JSON.stringify({seq: this.seqId++});
          break;

        case 'unpublish':
          eventItem = new ReportItem({
            miniappUnpublish: true
          });
          eventItem.uri = 8005;
          eventItem['miniappUnpublish'].header = header;
          eventItem['miniappUnpublish'].ec = body.ec;
          eventItem['miniappUnpublish'].details = JSON.stringify({seq: this.seqId++});
          break;

        case 'subscribe':
          eventItem = new ReportItem({
            miniappSubscribe: true
          });
          eventItem.uri = 8006;
          eventItem['miniappSubscribe'].header = header;
          eventItem['miniappSubscribe'].ec = body.ec;
          eventItem['miniappSubscribe'].pullurl = body.pullurl;
          eventItem['miniappSubscribe'].peerid = body.peerid;
          eventItem['miniappSubscribe'].details = JSON.stringify({seq: this.seqId++});
          break;

        case 'unsubscribe':
          eventItem = new ReportItem({
            miniappUnsubscribe: true
          });
          eventItem.uri = 8007;
          eventItem['miniappUnsubscribe'].header = header;
          eventItem['miniappUnsubscribe'].ec = body.ec;
          eventItem['miniappUnsubscribe'].peerid = body.peerid;
          eventItem['miniappUnsubscribe'].details = JSON.stringify({seq: this.seqId++});
          break;
        
        case 'leave':
          eventItem = new ReportItem({
            miniappLeave: true
          });
          eventItem.uri = 8008;
          eventItem['miniappLeave'].header = header;
          eventItem['miniappLeave'].ec = body.ec;
          eventItem['miniappLeave'].details = JSON.stringify({seq: this.seqId++});
          break;
        
        case 'pusherState':
          eventItem = new ReportItem({
            miniappPusherState: true
          });
          eventItem.uri = 8010;
          eventItem['miniappPusherState'].header = header;
          eventItem['miniappPusherState'].url = body.url;
          eventItem['miniappPusherState'].code = body.code;
          eventItem['miniappPusherState'].status = body.status;
          eventItem['miniappPusherState'].error = body.error;
          eventItem['miniappPusherState'].details = JSON.stringify({seq: this.seqId++});
          break;

        case 'playerState':
          eventItem = new ReportItem({
            miniappPlayerState: true
          });
          eventItem.uri = 8011;
          eventItem['miniappPlayerState'].header = header;
          eventItem['miniappPlayerState'].url = body.url;
          eventItem['miniappPlayerState'].code = body.code;
          eventItem['miniappPlayerState'].status = body.status;
          eventItem['miniappPlayerState'].error = body.error;
          eventItem['miniappPlayerState'].details = JSON.stringify({seq: this.seqId++});
          break;

        case 'serverEvent':
          eventItem = new ReportItem({
            miniappServerEvent: true
          });
          eventItem.uri = 8009;
          eventItem['miniappServerEvent'].header = header;
          eventItem['miniappServerEvent'].action = body.action;
          eventItem['miniappServerEvent'].details = JSON.stringify(body.details);
          eventItem['miniappServerEvent'].req_ts = body.req_ts;
          break;
        
      }
    }
    
    this._cache.push(eventItem);
    if(!this.isReporting && this._cache.length > 0) {
      var eventItem = this._cache.shift();
      this._report(eventItem);
    }
  }

  _report(eventItem) {
    this.isReporting = true;
    this.client.Report(eventItem, (err) => {
      if(!err) {
        LOG.debug("Report succcess: ", eventItem);
        var nextEventItem = this._cache.shift();
        if(nextEventItem) {
          this._report(nextEventItem);
        } else {
          this.isReporting = false;
        }
      } else {
        LOG.debug("Report failed: ", err, eventItem);
        var delay = this.retryCount++ < 2 ? 200 : 10*1000;
        setTimeout(() => {
          this._report(eventItem);
        }, delay);
      }
    });
  }
}

export default ReportManager;