const BLIND = -1;
const DEBUG = 0;
const INFO = 1;
const WARN = 2;
const ERROR = 3;
const NONE = 4;

class Log{
  constructor() {
    this.level = ERROR;
    this.onLog = null;
  }

  setLogLevel(level) {
    if(typeof level !=  "number" || !(level <= 4 && level >= -1)) {
      throw Error('Invaild level');
    } else {
      this.level = level; 
    }
  }
  log() {
    let args = Array.prototype.slice.call(arguments);
    this.onLog && this.onLog(JSON.stringify(args));
    console.log(...args);
  }
  error() {
    let args = Array.prototype.slice.call(arguments);
    this.onLog && this.onLog(JSON.stringify(args));
    console.error(...args);
  }

  blind() {
    let args = Array.prototype.slice.call(arguments);
    this.level <= BLIND && this.log(`${new Date()} [BLIND]: `, ...args);
  } 

  debug() {
    let args = Array.prototype.slice.call(arguments);
    this.level <= DEBUG && this.log(`${new Date()} [DEBUG]: `, ...args);
  }

  info(log) {
    this.level <= INFO && this.log(`${new Date()} [INFO]: ${log}`);
  }

  warn(log) {
    this.level <= WARN && this.log(`${new Date()} [WARN]: ${log}`);
  }

  error(log) {
    this.level <= ERROR && this.error(`${new Date()} [ERROR]: ${log}`);
  }

}


const LOG = new Log();

export default LOG;
export {
  Log
}
