import { VERSION } from './Config'
import { isWeiXin } from './Helper'
import LOG from './Log';
import { setTimeout } from 'timers';

const ERROR_NO_NEED_RECONNECT = [
  // "invalid key"
  'service unavailable',
  'no channel',
  'too many users',
  'invalid key',
  'master available',
  'invalid channel name',
  'internal error',
  'no authorized',
  'dynamic key timeout',
  'no active status',
  'invalid uid',
  'dynamic key expired',
  'vendor over load',
];
const CHOOSE_SERVER_LIST = [
  "https://miniapp-1.agoraio.cn/appcenter/1-1-1/getWorkerManager/",
  "https://miniapp-2.agoraio.cn/appcenter/1-1-1/getWorkerManager/",
  "https://miniapp-3.agoraio.cn/appcenter/1-1-1/getWorkerManager/",
  "https://miniapp-4.agoraio.cn/appcenter/1-1-1/getWorkerManager/",

]

const addressRedirect = servers => {
  return servers.map(url => {
    url = url.address
    let arr = url.split('.');
    let length = arr.length;
    return `wss://miniapp.agoraio.cn/${arr[length-4]}-${arr[length-3]}-${arr[length-2]}-${arr[length-1]}/api`
  })
}

const getServerList = (option) => {
  const reportManager = option.reportManager;
  let payload = {
    command: "convergeAllocateEdge",
    sid: option.sid,
    appId: option.appId,
    token: option.appId,
    uid: option.uid + '',
    cname: option.channel,
    ts: Math.floor(Date.now() / 1000),
    version: VERSION,
    seq: 0,
    requestId: 1
  };

  return new Promise((resolve, reject) => {
    let failedCounter = 0;
    const failed = (err, url) => {
      try {
        reportManager.report('chooseServer', {
          header: {
            sid: option.sid,
            cname: option.channel,
            uid: option.uid,
            success: false
          },
          body: {
            csurl: url,
            ec: JSON.stringify(err)
          }
        })
      } catch (error) {}
      failedCounter++;
      if(failedCounter === 4) {
        reject(err);
      }
      return 
    }

    const request = csIndex => {
      var url = CHOOSE_SERVER_LIST[csIndex];
      //..........................................................................................................
      wx.request({
        url: url, 
        data: payload,
        header: {
          'content-type': 'application/json'
        },
        method: 'POST',
        success: (res) => {
          // TODO : data may be null
          let data = res.data;
          LOG.debug('From choose server', res);
          if(data.code !== 200) {
            failed({
              code: 901,
              reason: data.error || 'choose server fail'
            }, url);
          } else {
            let servers = addressRedirect(data.servers);
            reportManager.report('chooseServer', {
              header: {
                sid: option.sid,
                cname: option.channel,
                uid: option.uid,
                success: true
              },
              body: {
                csurl: url,
                gatewaylist: servers
              }
            })
            resolve({
              serverlist: servers, 
              url: url
            });
          }
        },
        fail: (err) => {
          failed({
            code: 901,
            reason: err.errMsg || 'choose server fail'
          }, url);
        }
      })
      //..........................................................................................................
    }
    CHOOSE_SERVER_LIST.map((server, index) => {
      request(index);
    })
    
  })
}

export {
  getServerList
}


  


