import {random, generateSessionId, is32Uint} from './Helper'; 
import * as config from './Config';
import Worker from './Worker';
import mitt from 'mitt';
import ReportManager from './Report';
import  { ChannelMediaRelayConfiguration } from "./ChannelMediaRelay.ts"; 
import {
  isEmpty,
  isValidToken,
  isValidChannelName,
} from "./utils/Check";

class Client {
  constructor(option) {
    this.emitter = new mitt();
    this.on = this.emitter.on;
    this.off = this.emitter.off;
    this.sid = generateSessionId();
    this.pushUrl = null;
    this.playUrls = new Map();
    this.servers = option && option.servers;
    this.reportManager = new ReportManager();
    this.role = 'broadcaster';
    
    this.emitter.on('stream-removed', evt => {
      if(!this.playUrls) return;
      this.playUrls.delete(evt.uid);
    })
  }

  init(appId, onSuccess, onFailure) {
    if(!appId || typeof appId !== 'string' || appId.toLowerCase() === 'null') {
      throw Error("Invalid appId");
    }
    this.appId = appId.trim();
    this.reportManager.report('sessionStart', {
      header: {
        sid: this.sid,
        lts: +new Date(), 
        success: true,
      },
      body: {
        appId : appId,
      }
    })
    onSuccess && onSuccess();
  }

  setRole(role, onSuccess, onFailure) {
    if(!role === 'broadcaster' && !role === 'audience') {
      throw Error("Invalid role");
    } 
    this.role = role;
    if(this.worker) {
      this.worker.setRole(role).then((res) => {
        setTimeout(() => {
          if(!res) return onSuccess && onSuccess();
          onSuccess && onSuccess({
            updateURL: res['update-url']
          });
        }, 1);
      }).catch(() => {
        onFailure && onFailure();
      })
    }
  }

  rejoin(channelKey, channel, uid, uids, onSuccess, onFailure) {
    if(this.worker) {
      return;
    }
    if(!channelKey) {
      channelKey = this.appId;
    } else if(typeof channelKey !== 'string' || channelKey.toLowerCase() === 'null'){
      throw Error("Invalid channelKey");
    }

    if(!channel || typeof channel !== 'string' || channel.toLowerCase() === 'null' || channel.length > 128) {
      throw Error("Invalid channel");
    }
    this.uid = uid || random(10000000);
    this.uid = Number.parseInt(this.uid);
    if(!is32Uint(this.uid)) {
      throw new Error('Invalid uid type');
    }
    this.channel = channel;

    this.worker = new Worker({
      appId: this.appId,
      channel: this.channel,
      uid: this.uid,
      sid: this.sid,
      role: this.role,
      servers: this.servers,
      channelKey: channelKey,
      reportManager: this.reportManager
    }, this.emitter);

    this.worker.connect()
      .then(() => this.worker.rejoin(uids))
      .then(() => {
        try {
          this.reportManager.report('join', {
            header: {
              sid: this.sid,
              cname: this.channel, 
              uid: this.uid,
              success: true
            }, 
            body: {
              isrejoin: true,
              appId: this.appId,
              wsurl: this.worker && this.worker.getWSUrl()
            }
          });
        } catch (error) {}
        setTimeout(function() {
          onSuccess && onSuccess(this.uid);
        }, 1);
        
      })
      .catch(err => {
        try {
          this.reportManager.report('join', {
            header: {
              sid: this.sid,
              cname: this.channel, 
              uid: this.uid,
              success: false
            }, 
            body: {
              ec: JSON.stringify(err),
              appId: this.appId,
              wsurl: this.worker && this.worker.getWSUrl()
            }
          });
        } catch (error) {}
        this.destroy();
        onFailure && onFailure(err)
      });
  }

  join(channelKey, channel, uid, onSuccess, onFailure) {
    if(this.worker) {
      return;
    }
    if(!channelKey) {
      channelKey = this.appId;
    } else if(typeof channelKey !== 'string' || channelKey.toLowerCase() === 'null'){
      throw Error("Invalid channelKey");
    }

    if(!channel || typeof channel !== 'string' || channel.toLowerCase() === 'null' || channel.length > 128) {
      throw Error("Invalid channel");
    }
    this.uid = uid || random(10000000);
    this.uid = Number.parseInt(this.uid);
    if(!is32Uint(this.uid)) {
      throw new Error('Invalid uid type');
    }
    this.channel = channel;

    this.worker = new Worker({
      appId: this.appId,
      channel: this.channel,
      uid: this.uid,
      sid: this.sid,
      role: this.role,
      servers: this.servers,
      channelKey: channelKey,
      reportManager: this.reportManager
    }, this.emitter);

    this.worker.connect()
      .then(() => this.worker.join())
      .then(() => {
        try {
          this.reportManager.report('join', {
            header: {
              sid: this.sid,
              cname: this.channel, 
              uid: this.uid,
              success: true
            }, 
            body: {
              isrejoin: false,
              appId: this.appId,
              wsurl: this.worker && this.worker.getWSUrl()
            }
          });
        } catch (error) {}
        setTimeout(function() {
          onSuccess && onSuccess(this.uid);
        }, 1);
        
      })
      .catch(err => {
        try {
          this.reportManager.report('join', {
            header: {
              sid: this.sid,
              cname: this.channel, 
              uid: this.uid,
              success: false
            }, 
            body: {
              ec: JSON.stringify(err),
              appId: this.appId,
              wsurl: err.wsurl || (this.worker && this.worker.getWSUrl())
            }
          });
        } catch (error) {}
        this.destroy();
        onFailure && onFailure(err)
      });
  }

  publish(onSuccess, onFailure) {
    this._checkWorker();
    this.worker.publish()
    .then(res => {
      this.pushUrl = res.url;
      try {
        this.reportManager.report('publish', {
          header: {
            sid: this.sid,
            cname: this.channel, 
            uid: this.uid,
            success: true
          }, 
          body: {
            pushurl: res.url
          }
        });
      } catch (error) {}
      setTimeout(() => {
        onSuccess && onSuccess(res.url);
      }, 1);
    })
    .catch(err => {
      this.reportManager.report('publish', {
        header: {
          sid: this.sid,
          cname: this.channel, 
          uid: this.uid,
          success: false
        }, 
        body: {
          ec: JSON.stringify(err)
        }
      });
      onFailure && onFailure(err);
    });
  }

  unpublish(onSuccess, onFailure) {
    this._checkWorker();
    this.worker.unpublish()
    .then(res => {
      this.pushUrl = null;
      try {
        this.reportManager.report('unpublish', {
          header: {
            sid: this.sid,
            cname: this.channel, 
            uid: this.uid,
            success: true
          }, 
          body: {}
        });
      } catch (error) {}
      setTimeout(function() {
        onSuccess && onSuccess();
      },1);
    })
    .catch(err => {
      this.reportManager.report('unpublish', {
        header: {
          sid: this.sid,
          cname: this.channel, 
          uid: this.uid,
          success: false
        }, 
        body: {
          ec: JSON.stringify(err)
        }
      });
      onFailure && onFailure(err)
    });
  }
  
  subscribe(uid, onSuccess, onFailure) {
    // var url = `rtmp://${config.DOMAIN_NAME}/live/${this.appId}_${Base64.encode(this.channel)}_${uid}`;
    this._checkUid(uid);
    this._checkWorker();
    this.worker.subscribe(uid)
      .then(res => {
        this.playUrls.set(uid, res.url);
        try {
          this.reportManager.report('subscribe', {
            header: {
              sid: this.sid,
              cname: this.channel, 
              uid: this.uid,
              success: true
            }, 
            body: {
              pullurl: res.url,
            }
          });
        } catch (error) {}
        setTimeout(() => {
          onSuccess && onSuccess(res.url, res.rotation);
        }, 1);
      })
      .catch(err => {
        this.reportManager.report('subscribe', {
          header: {
            sid: this.sid,
            cname: this.channel, 
            uid: this.uid,
            success: false
          }, 
          body: {
            ec: JSON.stringify(err),
          }
        });
        onFailure && onFailure(err)
      });
  }

  unsubscribe(uid, onSuccess, onFailure) {
    this._checkUid(uid);
    this._checkWorker();
    this.worker.unsubscribe(uid)
      .then(res => {
        this.playUrls.delete(uid);
        try {
          this.reportManager.report('unsubscribe', {
            header: {
              sid: this.sid,
              cname: this.channel, 
              uid: this.uid,
              success: false
            }, 
            body: {}
          });
        } catch (error) {}
        setTimeout(() => {
          onSuccess && onSuccess();
        }, 1);
      })
      .catch(err => {
        this.reportManager.report('unsubscribe', {
          header: {
            sid: this.sid,
            cname: this.channel, 
            uid: this.uid,
            success: false
          }, 
          body: {
            ec: JSON.stringify(err)
          }
        });
        onFailure && onFailure(err)
      });
  }

  mute(uid, target, onSuccess, onFailure) {
    this._checkUid(uid);
    this._checkWorker();
    if(target !== 'video' && target !== 'audio' && target !== 'all') {
      throw new Error('target should be "video", "audio" or "all"');
    }
    this.worker.mute(uid, target)
      .then(res => {
        setTimeout(() => {
          onSuccess && onSuccess();
        }, 1);
      }).catch(err => {
        onFailure && onFailure(err);
      })
  }

  unmute(uid, target, onSuccess, onFailure) {
    this._checkUid(uid);
    this._checkWorker();
    if(target !== 'video' && target !== 'audio' && target !== 'all') {
      throw new Error('target should be "video", "audio" or "all"');
    }
    this.worker.unmute(uid, target)
      .then(res => {
        setTimeout(() => {
          onSuccess && onSuccess();
        }, 1);
      }).catch(err => {
        onFailure && onFailure(err);
      })
  }

  muteLocal(target, onSuccess, onFailure) {
    this._checkWorker();
    if(target !== 'video' && target !== 'audio' && target !== 'all') {
      throw new Error('target should be "video", "audio" or "all"');
    }
    this.worker.muteLocal(target)
      .then(res => {
        setTimeout(() => {
          onSuccess && onSuccess();
        }, 1);
      }).catch(err => {
        onFailure && onFailure(err);
      })
  }

  unmuteLocal(target, onSuccess, onFailure) {
    this._checkWorker();
    if(target !== 'video' && target !== 'audio' && target !== 'all') {
      throw new Error('target should be "video", "audio" or "all"');
    }
    this.worker.unmuteLocal(target)
      .then(res => {
        setTimeout(() => {
          onSuccess && onSuccess();
        }, 1);
      }).catch(err => {
        onFailure && onFailure(err);
      })
  }

  leave(onSuccess, onFailure) {
    if(this.worker && this.worker.isWSConnected()) {
      this.worker.leave()
      .then(res => {
        try {
          this.reportManager.report('leave', {
            header: {
              sid: this.sid,
              cname: this.channel, 
              uid: this.uid,
              success: true
            }, 
            body: {}
          });
        } catch (error) {}
        this.destroy(() => {
          setTimeout(() => {
            onSuccess && onSuccess();
          }, 1);
        })
      }).catch(err => {
        onFailure && onFailure(err);
      })
    }
  }

  _checkUid(uid) {
    if(!uid || typeof uid !== 'number' || !is32Uint(uid)) {
      throw new Error('Invalid uid type');
    }
  }

  _checkWorker() {
    if(!this.worker || !this.worker.isWSConnected()) {
      throw new Error('Disconnected from server');
    }
  }

  isConnected() {
    if(!this.worker || !this.worker.isWSConnected()) {
      return false;
    } else {
      return true;
    }
  }

  updatePusherStateChange(e) {
    if(this.pushUrl) {
      let code = e.detail.code;
      if(code == 1008 || code < 0) {
        let success = code == 1008 ? true : false;
        // 上报事件
        this.reportManager && this.reportManager.report('pusherState', {
          header: {
            sid: this.sid,
            cname: this.channel, 
            uid: this.uid,
            success: success
          }, 
          body: {
            url: this.pushUrl,
            code: code
          }
        });
      }
    }
  }

  updatePlayerStateChange(uid, e) {
    let playUrl = this.playUrls.get(uid);
    if(playUrl) {
      let code = e.detail.code;
      if(code == 2004 || (code < 0 || code > 3000)) {
        let success = code == 2004 ? true : false;
        // 上报事件
        this.reportManager && this.reportManager.report('playerState', {
          header: {
            sid: this.sid,
            cname: this.channel, 
            uid: uid,
            success: success
          }, 
          body: {
            url: playUrl,
            code: code
          }
        });
      }
    }
  }

  updatePusherNetStatus(e) {
    
  }

  updatePlayerNetStatus(uid, e) {

  }

  updatePusherError(e) {
    
  }

  updatePlayerError(uid, e) {

  }

  send(option, onSuccess, onFailure) {
    this._checkWorker();
    this.worker.send(option).then(res => {
      onSuccess && onSuccess(res);
    }).catch(err => {
      onFailure && onFailure(err);
    })
  }

  updatePushUrl(onSuccess, onFailure) {
    this._checkWorker();
    this.worker.updatePushUrl().then(res => {
      setTimeout(function() {
        onSuccess && onSuccess(res);
      }, 1);
    }).catch(err => {
      onFailure && onFailure(err);
    })
  }

  destroy(onSuccess, onFailure) {
    if(this.worker) {
      this.worker.destroy(() => {
        this.pushUrl = null;
        this.playUrls = null;
        onSuccess && onSuccess();
      });
      this.worker = null;
    } else {
      this.pushUrl = null;
      this.playUrls = null;
      onSuccess && onSuccess();
    }
  }

  startChannelMediaRelay(config, callback) {
    /**
     * param check
     */
    if(!(config instanceof ChannelMediaRelayConfiguration)) {
      throw "Configration should be instance of [ChannelMediaRelayConfiguration]";
    }
    const srcInfo = config.getSrcChannelMediaInfo();
    const destInfo = config.getDestChannelMediaInfos();

    if(isEmpty(srcInfo)) {
      throw "srcChannelMediaInfo should not be empty";
    }
    if(isEmpty(destInfo) || destInfo.length === 0) {
      throw "destChannelMediaInfos should not be empty";
    }

    if(!is32Uint(srcInfo.uid)) {
      throw "Invalid uid in srcChannelMediaInfo";
    }
    if(!isValidChannelName(srcInfo.channelName)) {
      throw "Invalid channelName in srcChannelMediaInfo";
    }
    if(srcInfo.token && !isValidToken(srcInfo.token)) {
      throw "Invalid token in srcChannelMediaInfo";
    }

    destInfo.forEach((info) => {
      if(!is32Uint(info.uid)) {
        throw "Invalid uid in destChannelMediaInfo";
      }
      if(!isValidChannelName(info.channelName)) {
        throw "Invalid channelName in destChannelMediaInfo";
      }
      if(info.token && !isValidToken(info.token)) {
        throw "Invalid token in destChannelMediaInfo";
      }
    });

    if(!this.worker) {
      throw "startChannelMediaRelay should be used after join";
    }

    this.worker.startChannelMediaRelay(config).then(() => {
      callback && callback();
    }).catch((e) => {
      callback && callback(e);
    });
  }

  updateChannelMediaRelay(config, callback) {
    /**
     * param check
     */
    if(!(config instanceof ChannelMediaRelayConfiguration)) {
      throw "Configration should be instance of [ChannelMediaRelayConfiguration]";
    }
    const srcInfo = config.getSrcChannelMediaInfo();
    const destInfo = config.getDestChannelMediaInfos();

    if(isEmpty(srcInfo)) {
      throw "srcChannelMediaInfo should not be empty";
    }
    if(isEmpty(destInfo) || destInfo.length === 0) {
      throw "destChannelMediaInfos should not be empty";
    }

    if(!is32Uint(srcInfo.uid)) {
      throw "Invalid uid in srcChannelMediaInfo";
    }
    if(!isValidChannelName(srcInfo.channelName)) {
      throw "Invalid channelName in srcChannelMediaInfo";
    }
    if(srcInfo.token && !isValidToken(srcInfo.token)) {
      throw "Invalid token in srcChannelMediaInfo";
    }

    destInfo.forEach((info) => {
      if(!is32Uint(info.uid)) {
        throw "Invalid uid in destChannelMediaInfo";
      }
      if(!isValidChannelName(info.channelName)) {
        throw "Invalid channelName in destChannelMediaInfo";
      }
      if(info.token && !isValidToken(info.token)) {
        throw "Invalid token in destChannelMediaInfo";
      }
    });

    if(!this.worker) {
      throw "updateChannelMediaRelay should be used after join";
    }

    this.worker.updateChannelMediaRelay(config).then(() => {
      callback && callback();
    }).catch((e) => {
      callback && callback(e);
    });
  }

  stopChannelMediaRelay(callback) {
    this.worker.stopChannelMediaRelay().then(() => {
      callback && callback();
    }).catch((e) => {
      callback && callback(e);
    });
  }
  
}

export default Client;
