import LOG from './Log';
import Socket from './WXSocket';
import MessageManager from './Message';
import mitt from 'mitt'
import ChannelMediaRelayController, {
  CHANNEL_MEDIA_RELAY_EVENT, 
  CHANNEL_MEDIA_RELAY_STATE, 
  ChannelMediaError,
  CHANNEL_MEDIA_RELAY_ERROR,
  CHANNEL_MEDIA_ERROR} from "./ChannelMediaRelay.ts";


class Worker {
  constructor(option, emitter) {
    this.appId = option.appId;
    this.channel = option.channel;
    this.uid = option.uid;
    this.sid = option.sid;
    this.role = option.role;
    this.channelKey = option.channelKey;
    this.clientId = "client";
    this.option = option;
    this.timers = new Set();
    this.reportManager = option.reportManager;
    
    this.clientEmitter = emitter;
    this.emitter = new mitt();
    this.on = this.emitter.on;
    this.off = this.emitter.off;
    this.emit = this.emitter.emit;

    this.join = this.join.bind(this);
    this.msgManager = new MessageManager(option); 
  }

  connect() {
    return new Promise((resolve, reject) => {
      this.ws = new Socket(this.option, this.emitter);
      this.ws.connect();
      this.on('onmessage', this._onMessage.bind(this));
      const onopen = () => {
        this.off && this.off('onopen', onopen);
        this._ping();
        resolve();
      }
      this.on('socketError', reject);
      this.on('onopen', onopen);
      this.on('onclose', () => this.clientEmitter.emit('error', {
        code: 904,
        reason: 'websocket disconnected',
      }));
    })
  }

  _onMessage(msg) {
    if(msg && msg.data) {
      let data = JSON.parse(msg.data);
      if(data && data.command !== 'pong') {
        LOG.debug('worker message: ', data);
        try {
          this.reportManager.report('serverEvent', {
            header: {
              sid: this.sid,
              cname: this.channel, 
              uid: this.uid,
              success: true
            }, 
            body: {
              action: "serverEvent",
              details: data,
              req_ts: +new Date(),
            }
          });
        } catch (error) {}
      }
      if(data.requestId) {
        this.emit(data.requestId, data);
      }
      if(data.command === 'serverStatus' ) {
        if(data.code && data.code !== 200) {
          if(data.code === 503) {
            return;
          }
          this.clientEmitter.emit('error', {
            code: data.code,
            reason: data.reason || 'server error',
          }); 
        }
        if(data.serverStatus) {
          LOG.info(`Receive message from server: ${JSON.stringify(data.serverStatus)}`);
          let action = data.serverStatus.action;
          // audio-mute 和 video-mute 这两个消息不能透传
          // 因为他把mute/unmute两个action写成了一个
          // 抛出的事件要对齐rtc的命名
          if(action == "audio-mute") {
            if(data.serverStatus.mute) {
              return this.clientEmitter.emit("mute-audio", {uid: data.serverStatus.uid});
            } else {
              return this.clientEmitter.emit("unmute-audio", {uid: data.serverStatus.uid});
            }
          }
          if(action == "video-mute") {
            if(data.serverStatus.mute) {
              return this.clientEmitter.emit("mute-video", {uid: data.serverStatus.uid});
            } else {
              return this.clientEmitter.emit("unmute-video", {uid: data.serverStatus.uid});
            }
          }
          this.clientEmitter.emit(action, data.serverStatus);
        }
      }
    }
  }

  _sendMessage(msg) {
    if(!this.ws) return;
    return new Promise((resolve, reject) => {
      if(!this.isWSConnected()){
        return reject({
          code: 904,
          reason: 'websocket disconnected',
          wsurl: this.getWSUrl()
        });
      }
      if(msg.command !== 'ping') {
        LOG.debug('send to woker: ', msg);
      }
      let requestId = msg ? msg.requestId : null;
      let timer;
      const handleResponse = (res) => {
        if(!this.ws) return;
        this.off(requestId, handleResponse);
        clearTimeout(timer);
        this.timers.delete(timer);
        if (res.code == 200){
          return resolve(res.serverResponse);
        }else{
          return reject({
            code: res.code,
            reason: res.msg || res.reason,
            serverResponse: res.serverResponse,
            wsurl: this.getWSUrl()
          });
        }
      };

      if (requestId){
        this.ws.sendMessage(JSON.stringify(msg));
        this.on(requestId, handleResponse);
        timer = setTimeout(() => handleResponse({code: 903, reason: 'time out'}), 10000);
        this.timers.add(timer);
      }else{
        return reject({code: 902, reason: 'no requestid'}); 
      }
    })
  }

  _ping() {
    // ping workermanager
    this.pingTimer = setInterval(() => {
      this.ws.sendMessage(JSON.stringify(this.msgManager.ping()));
    }, 3000);

    // ping worker
    // 8.22: 王昊天说删了
    // this.ping2Timer = setInterval(() => {
    //   this.ws.sendMessage(JSON.stringify(this.msgManager.ping2()));
    // }, 3000);
  }

  setRole(role) {
    return this._sendMessage(this.msgManager.setRole(role));
  }

  rejoin(uids) {
    return this._sendMessage(this.msgManager.rejoin(this.appId, this.channel, this.uid, this.channelKey, uids, this.role));
  }

  join() {
    return this._sendMessage(this.msgManager.join(this.appId, this.channel, this.uid, this.channelKey, this.role));
  }

  send(option) {
    return this._sendMessage(this.msgManager.send(option));
  }

  updatePushUrl() {
    return this._sendMessage(this.msgManager.updatePushUrl());
  }

  publish() {
    return this._sendMessage(this.msgManager.publish(this.uid));
  }

  unpublish() {
    return this._sendMessage(this.msgManager.unpublish(this.uid));
  }

  subscribe(uid) {
    return this._sendMessage(this.msgManager.subscribe(uid));
  }

  unsubscribe(uid) {
    return this._sendMessage(this.msgManager.unsubscribe(uid));
  }

  mute(uid, target) {
    return this._sendMessage(this.msgManager.mute(uid, target));
  }

  unmute(uid, target) {
    return this._sendMessage(this.msgManager.unmute(uid, target));
  }

  muteLocal(target) {
    return this._sendMessage(this.msgManager.muteLocal(this.uid, target));
  }

  unmuteLocal(target) {
    return this._sendMessage(this.msgManager.unmuteLocal(this.uid, target));
  }

  leave() {
    return this._sendMessage(this.msgManager.leave(this.uid));
  }

  isWSConnected() {
    return !!this.ws && this.ws.isConnected();
  }

  getWSUrl() {
    return this.ws.serverlist[this.ws.index];
  }

  destroy(callback) {
    clearInterval(this.pingTimer);
    // clearInterval(this.ping2Timer);
  
    this.timers.forEach(timer => {
      clearInterval(timer);
    });
    this.timers = null;

    this.ws && this.ws.destroy(() => {
      this.ws = null;

      this.emitter = null;
      this.on = null;
      this.off = null;
      this.emit = null;
      callback && callback();
    })
  }

  async startChannelMediaRelay(config) {
    if(this.channelMediaRelayController) {
      throw new ChannelMediaError("RELAY_ALREADY_START", "", CHANNEL_MEDIA_RELAY_ERROR.RELAY_ALREADY_START);
    }

    const onEvent = (e) => {
      this.clientEmitter.emit({
        type: "channel-media-relay-event",
        code: e,
      });
    }
    const onState = (e, code, data) => {
      if(e === CHANNEL_MEDIA_RELAY_STATE.RELAY_STATE_FAILURE && code === CHANNEL_MEDIA_RELAY_ERROR.SERVER_CONNECTION_LOST) {
        this.channelMediaRelayController.dispose();
        /** 
         * 理论上，当跨频道连麦的连接启动失败或者断开的时候，都应该重置所有的状态。
         * 但是由于几乎所有的功能依赖join成功，所以在重连gateway的时候，我们会清空其他的连接（推拉流，跨频道连麦，p2p）,在重连并join成功的时候再恢复这些连接
         * 这就导致一个问题，在断网情况下，如果 SERVER_CONNECTION_LOST 触发，在rejoin后就不会进行 跨频道连麦 的重连，因为是根据 channelMediaConfig 的存在决定是否重连（见 Client.js 中的rejoin ）
         * 所以在这里不重置channelMediaConfig
         */
        // this.channelMediaConfig = null;
        this.channelMediaRelayController = null;
      }
      this.clientEmitter.emit({
        type: "channel-media-relay-state",
        state: e,
        code: code,
        data
      });
    }

    const joinInfo = {
      token: this.appId,
      appId: this.appId,
      cname: this.channel,
      uid: this.uid,
      sid: this.sid,
      clientId: this.clientId
    }

    this.channelMediaRelayController = new ChannelMediaRelayController(joinInfo, this.clientId);
    this.channelMediaRelayController.on("event", onEvent);
    this.channelMediaRelayController.on("state", onState);
    try {
      await this.channelMediaRelayController.startChannelMediaRelay(config);
    } catch (e) {
      LOG.debug(`[${this.clientId}] startChannelMediaRelay exception: ${JSON.stringify(e)}`);
      const stateCode = CHANNEL_MEDIA_RELAY_STATE.RELAY_STATE_FAILURE;
      let code = "";
      if(e instanceof ChannelMediaError) {
        if(e.data === "SetSourceChannelStatus" || (e.data && e.data.serverResponse && e.data.serverResponse.command === "SetSourceChannel")) {
          code = "FAILED_JOIN_SRC";
        } else if(e.data === "SetDestChannelStatus" || (e.data && e.data.serverResponse && e.data.serverResponse.command === "SetDestChannel")) {
          code = "FAILED_JOIN_DEST";
        } else if(e.data && e.data.serverResponse && e.data.serverResponse.command === "StartPacketTransfer") {
          code = "FAILED_PACKET_SENT_TO_DEST";
        } else if(e.message === CHANNEL_MEDIA_ERROR.REQUEST_TIMEOUT || e.message === CHANNEL_MEDIA_ERROR.WAIT_STATUS_TIMEOUT) {
          code = "SERVER_NO_RESPONSE";
        } else if(e.message === CHANNEL_MEDIA_ERROR.REQUEST_FAILED || e.message === CHANNEL_MEDIA_ERROR.WAIT_STATUS_ERROR) {
          code = "SERVER_ERROR_RESPONSE";
        } else if(e.message === CHANNEL_MEDIA_ERROR.AP_REQUEST_TIMEOUT) {
          code = "NO_RESOURCE_AVAILABLE";
        } else if(e.message === CHANNEL_MEDIA_ERROR.WS_ABORT || e.message === CHANNEL_MEDIA_ERROR.AP_REQUEST_ABORT) {
          code = "OPERATION_ABORT";
        } else {
          code = "INTERNAL_ERROR";
        }
      } else {
        code = "INTERNAL_ERROR";
      }
      
      this.clientEmitter.emit({
        type: "channel-media-relay-state",
        state: stateCode,
        code: CHANNEL_MEDIA_RELAY_ERROR[code],
      });
      /** 
       * "任何错误都需要提示错误，然后退出跨直播间连麦"
       * "提示错误，然后本身状态变成初始状态"
       * "然后退出频道再进"
       */
      if(this.channelMediaRelayController) {
        this.channelMediaRelayController.dispose();
        this.channelMediaConfig = null;
        this.channelMediaRelayController = null;
      }
      this.clientEmitter.emit({
        type: "channel-media-relay-state",
        state: CHANNEL_MEDIA_RELAY_STATE.RELAY_STATE_IDLE,
      });
      throw new ChannelMediaError(code, null, CHANNEL_MEDIA_RELAY_ERROR[code]);
    }
    this.clientEmitter.emit({
      type: "channel-media-relay-state",
      state: CHANNEL_MEDIA_RELAY_STATE.RELAY_STATE_RUNNING,
      code: CHANNEL_MEDIA_RELAY_ERROR["RELAY_OK"],
    });
    this.channelMediaConfig = config;
  }

  async updateChannelMediaRelay(config) {
    if(!this.channelMediaRelayController) {
      throw new ChannelMediaError("RELAY_NOT_START", null, CHANNEL_MEDIA_RELAY_ERROR.RELAY_NOT_START);
    }
    try {
      await this.channelMediaRelayController.updateChannelMediaRelay(config);
    } catch (e) {
      LOG.debug(`[${this.clientId}] updateChannelMediaRelay exception: ${JSON.stringify(e)}`);
      const stateCode = CHANNEL_MEDIA_RELAY_STATE.RELAY_STATE_FAILURE;
      let code = "";
      if(e instanceof ChannelMediaError) {
      if(e.message === CHANNEL_MEDIA_ERROR.REQUEST_TIMEOUT || e.message === CHANNEL_MEDIA_ERROR.WAIT_STATUS_TIMEOUT) {
          code = "SERVER_NO_RESPONSE";
        } else if(e.message === CHANNEL_MEDIA_ERROR.REQUEST_FAILED || e.message === CHANNEL_MEDIA_ERROR.WAIT_STATUS_ERROR) {
          code = "SERVER_ERROR_RESPONSE";
        } else if(e.message === CHANNEL_MEDIA_ERROR.WS_ABORT || e.message === CHANNEL_MEDIA_ERROR.AP_REQUEST_ABORT) {
          code = "OPERATION_ABORT";
        } else {
          code = "INTERNAL_ERROR";
        }
      } else {
        code = "INTERNAL_ERROR";
      }
      this.clientEmitter.emit({
        type: "channel-media-relay-event",
        code: CHANNEL_MEDIA_RELAY_EVENT.PACKET_UPDATE_DEST_CHANNEL_REFUSED,
      });
      this.clientEmitter.emit({
        type: "channel-media-relay-state",
        state: stateCode,
        code: CHANNEL_MEDIA_RELAY_ERROR[code],
      });
      /** 
       * "任何错误都需要提示错误，然后退出跨直播间连麦"
       * "提示错误，然后本身状态变成初始状态"
       * "然后退出频道再进"
       */
      if(this.channelMediaRelayController) {
        this.channelMediaRelayController.dispose();
        this.channelMediaConfig = null;
        this.channelMediaRelayController = null;
      }
      this.clientEmitter.emit({
        type: "channel-media-relay-state",
        state: CHANNEL_MEDIA_RELAY_STATE.RELAY_STATE_IDLE,
      });
      throw new ChannelMediaError(code, null, CHANNEL_MEDIA_RELAY_ERROR[code]);
    }
    this.channelMediaConfig = config;
  }

  async stopChannelMediaRelay() {
    if(!this.channelMediaRelayController) {
      throw new ChannelMediaError("RELAY_NOT_START", null, CHANNEL_MEDIA_RELAY_ERROR.RELAY_NOT_START);
    }
    try {
      await this.channelMediaRelayController.stopChannelMediaRelay();
    } catch (e) {
      LOG.debug(`[${this.clientId}] stopChannelMediaRelay exception: ${JSON.stringify(e)}`);
      const stateCode = CHANNEL_MEDIA_RELAY_STATE.RELAY_STATE_FAILURE;
      let code = "";
      if(e instanceof ChannelMediaError) {
      if(e.message === CHANNEL_MEDIA_ERROR.REQUEST_TIMEOUT || e.message === CHANNEL_MEDIA_ERROR.WAIT_STATUS_TIMEOUT) {
          code = "SERVER_NO_RESPONSE";
        } else if(e.message === CHANNEL_MEDIA_ERROR.REQUEST_FAILED || e.message === CHANNEL_MEDIA_ERROR.WAIT_STATUS_ERROR) {
          code = "SERVER_ERROR_RESPONSE";
        } else {
          code = "INTERNAL_ERROR";
        }
      } else {
        code = "INTERNAL_ERROR";
      }
      this.clientEmitter.emit({
        type: "channel-media-relay-state",
        state: stateCode,
        code: CHANNEL_MEDIA_RELAY_ERROR[code],
      });
      /** 
       * "任何错误都需要提示错误，然后退出跨直播间连麦"
       * "提示错误，然后本身状态变成初始状态"
       * "然后退出频道再进"
       */
      if(this.channelMediaRelayController) {
        this.channelMediaRelayController.dispose();
        this.channelMediaConfig = null;
        this.channelMediaRelayController = null;
      }
      this.emit({
        type: "channel-media-relay-state",
        state: CHANNEL_MEDIA_RELAY_STATE.RELAY_STATE_IDLE,
      });
      throw new ChannelMediaError(code, null, CHANNEL_MEDIA_RELAY_ERROR[code]);
    }
    this.channelMediaConfig = null;
    this.channelMediaRelayController = null;
  }
}

export default Worker